import {VdlError} from '../core/errors/error';

/** @docs-private */
export class VdlInputContainerUnsupportedTypeError extends VdlError {
  constructor(type: string) {
    super(`Input type "${type}" isn't supported by vdl-input-container.`);
  }
}

/** @docs-private */
export class VdlInputContainerMissingVdlInputError extends VdlError {
  constructor() {
    super('vdl-input-container must contain an vdlInput directive. Did you forget to add vdlInput ' +
          'to the native input or textarea element?');
  }
}
